@Component({
  components: {
    WidgetParams,
    Navigation
  }
})
export default class Method extends Widget {
  @Prop() index!:number;
  @Prop() refIndex!:number;
  @Prop() widgetList:WidgetRef[] = [];
  pathProcessor = new PathProcessor();
  strMapObjChange = new StrMapObjChange();
  WidgetComponentName: string = "Method";
  StatusValue: string = "";
  pathId: string = "";
  userInputData = new Map<string, string>();
  pathwithVar: string = "";
  isShowPath: boolean = false;
  isShowParams: boolean = false;
  fontSize:number =  25;
  EditPathPoke : string =  "";
  curArg: {};

  config: WidgetConfig = {
    WidgetComponentName: "Method",
    data: {
      url: "",
      displayname:"",
      userInputData: ""
    }
  };

    EditData = {
        edit:{
            type:'method',
            parseUrl:'',
            url:this.config.data.url,
            index:this.refIndex,
        },
        props:{
            fontSize:this.fontSize,
            title:'',
            titleSize:'',
        },
        params:{
            PokedPath:this.EditPathPoke,
