        pokeAndUpdateUI(ref: string, sample: any) {
    Vue.nextTick(() => {
      ((this.$refs[ref] as Array<Widget>)[0] as Widget).samplePoke(sample);
      ((this.$refs[ref] as Array<Widget>)[0] as Widget).updateUI();
    });
